/* src/components/ProductList.css */
.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: rgb(225,203,228);
  margin-top:40px;
}
.product-card {
  width: 300px;
  background-color: #ffffff;
  border: 1px solid #ddd;
  margin: 10px;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.product-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
}
.product-details {
  padding: 10px;
}
.product-title {
  font-size: 16px;
  margin-bottom: 5px;
}
.product-price {
  color:black;
  font-weight: bold;
}
